#Navbar-nota{
    background-color: var(--my-coloryellow);
    border-bottom: 3px solid var(--my-colorblue);
}

#container-agregar-notas{
    border-radius: 20px;
    border: 2px solid var(--my-yellowoscuro);
    min-height: 80vh;
    width: 90%;
}

#agregar-textarea {
    resize: none;
}

.container-input {
    text-align: center;
    background: var(--my-coloryellow);
    border: 4px dashed var(--my-yellowoscuro);
    padding: 50px 0;
    border-radius: 15px;
    width: 100%;
    margin: 0 auto;
    margin-bottom: 20px;
}

.inputfile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.inputfile + label {
    max-width: 80%;
    font-size: 1.25rem;
    font-weight: 700;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    display: inline-block;
    overflow: hidden;
    padding: 0.625rem 1.25rem;
}

.iborrainputfile {
	font-size:16px; 
	font-weight:normal;
	font-family: 'Lato';
}

.inputfile-1 + label {
    color: #fff;
    background-color: var(--my-yellowoscuro);
    border-radius: 10px;
    border: 2px solid #D8DAA0;
}

.inputfile-1:focus + label,
.inputfile-1.has-focus + label,
.inputfile-1 + label:hover {
    background-color: #C7C993;
}

#imagen-agregar{
    object-fit: cover;
    object-position: center center;
    max-height: 150px;
    width: 100%;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;

}

#input-titulo{
    border: none;
    background-color: var(--my-yellowclaro);
    caret-color: var(--my-colorblue);
    font-size: xx-large;
}

#input-titulo::placeholder{
    padding-left: 5px;
}

#input-titulo:focus::placeholder{
    color: var(--my-yellowclaro);
}

#agregar-textarea{
    font-size: larger;
    caret-color: var(--my-colorblue);
    background-color:transparent !important;
    border:none !important;
}

#agregar-textarea:focus-visible{
    outline: none;
}

#agregar-textarea:focus::placeholder{
    color: var(--my-yellowclaro);
}


[contenteditable=true]:empty:before{
    content: attr(placeholder);
    pointer-events: none;
    display: block; /* For Firefox */
    color: #6C757D;
}